import React, { useRef } from 'react'
import { Container } from 'react-bootstrap';

import styled from 'styled-components';
import emailjs from 'emailjs-com';
import Swal from 'sweetalert2'


const Label = styled.label`
    margin: 20px 0;
    font-weight: bold;
`;

const ContainerStyled = styled(Container)`
    margin: 0 0 150px;
    padding: 0;
`;

const ButtonStyled = styled.button`
    background-color: black;
    color: white;
    border: none;
    font-weight: bold;
    font-size: 12px;
    transition: 50ms;
    width: 80%;
    height: 45px;
    border-radius: 25px;
    margin: auto;
    
    &:hover {
        background-color: white;
        color: black;
    }
`;

export default function Contact() {
    const form = useRef();
    const handleSubmit = (e) => {
        e.preventDefault();

        emailjs.sendForm('service_spm6n5d', 'template_rhbbiji', form.current, 'user_TSnPVC5E2IQqLQUH8M0A5')
        .then(() => {
            Swal.fire({
                position: 'middle',
                icon: 'success',
                title: 'El mensaje se ha enviado con exito!',
                showConfirmButton: false,
                timer: 1500
              })
        }, () => {
            Swal.fire({
                position: 'middle',
                icon: 'error',
                title: `No se ha podido enviar el mensaje.`,
                showConfirmButton: false,
                timer: 1500
              })
        });
        e.target.reset();
    }
    return (
        <ContainerStyled fluid className="d-flex justify-content-center align-items-center " id="contact-scroll" >
            <form className="row w-50" ref={form} onSubmit={handleSubmit} >
                <Label htmlFor="emailForm" className="px-0 text-white text-uppercase " ><i className="fas fa-user"></i> Nombre</Label>
                <input type="text" placeholder="Nombre..." name="user_name" className="p-2 rounded border-0 " />
                <Label htmlFor="emailForm" className="px-0  text-white text-uppercase" ><i className="fas fa-at"></i> Email</Label>
                <input type="text" placeholder="Email..." name="user_email" className="p-2 rounded border-0" />
                <Label htmlFor="emailForm" className="p-0 px-0 text-white text-uppercase"><i className="fas fa-paperclip"></i> Asunto</Label>
                <input type="text" placeholder="Asunto..." name="user_subject" className="p-2 rounded border-0" />
                <Label htmlFor="emailForm" className="p-0 px-0 text-white text-uppercase"><i className="fas fa-pen"></i> Dejar un mensaje</Label>
                <textarea type="text" placeholder="Mensaje..." name="message" rows="4"  className="p-2 rounded border-0"/>
                <ButtonStyled type="submit" className="mt-4">Enviar</ButtonStyled>
            <p className="mt-4 text-center text-white">¡Con gusto responderé todas tus dudas y consultas! :)</p>
            </form>
        </ContainerStyled>
    )
}
